'use client'
import React, { useState, useEffect } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { 
  ArrowLeft,
  Shield,
  User,
  MessageSquare,
  Phone,
  Laptop,
  CreditCard,
  Search,
  Bell,
  MessageCircle,
  BarChart3,
  IdCard,
  Briefcase,
  Settings,
  Gavel,
  Eye,
  Edit,
  XCircle,
  Download,
  Lock,
  Ban,
  Mail,
  MapPin,
  Menu,
  X,
  ArrowUp,
  ShoppingCart,
  Store,
  TruckIcon,
  DollarSign,
  Users,
  FileText,
  Camera,
  MessageCircleMore,
  Globe
} from 'lucide-react'

const PrivacyPolicy = () => {
  const router = useRouter()
  const [sidebarVisible, setSidebarVisible] = useState(false)
  const [activeSection, setActiveSection] = useState(1)
  const [scrollProgress, setScrollProgress] = useState(0)
  const [showBackToTop, setShowBackToTop] = useState(false)

  // Section data with buying/selling additions
  const sections = [
    { title: 'Introduction' },
    { title: 'Information We Collect' },
    { title: 'Marketplace Data Collection' },
    { title: 'How We Use Your Data' },
    { title: 'WhatsApp and Call Data' },
    { title: 'Buyer and Seller Interactions' },
    { title: 'Sharing of Information' },
    { title: 'Third-Party Services' },
    { title: 'Data Retention' },
    { title: 'Children\'s Privacy' },
    { title: 'Your Rights' },
    { title: 'Security' },
    { title: 'Changes to Policy' },
    { title: 'Contact Us' }
  ]

  const informationTypes = [
    {
      icon: User,
      title: 'Personal Information',
      description: 'Full name, phone number, email address, location, and profile details.'
    },
    {
      icon: MessageCircleMore,
      title: 'WhatsApp Data',
      description: 'Messages, voice notes, media, and other inputs sent to our WhatsApp bot.'
    },
    {
      icon: Phone,
      title: 'Call Data',
      description: 'Phone number, call duration, and conversation summaries for support.'
    },
    {
      icon: Laptop,
      title: 'Technical Information',
      description: 'Device type, IP address, browser type, and usage activity.'
    },
    {
      icon: CreditCard,
      title: 'Payment Data',
      description: 'Transaction history and payment methods (handled securely via third parties).'
    },
    {
      icon: ShoppingCart,
      title: 'Marketplace Activity',
      description: 'Items bought/sold, search history, preferences, and marketplace interactions.'
    }
  ]

  const marketplaceDataTypes = [
    {
      icon: Store,
      title: 'Seller Information',
      description: 'Business details, inventory, pricing, seller ratings, and transaction history.'
    },
    {
      icon: ShoppingCart,
      title: 'Buyer Information',
      description: 'Purchase history, wishlist items, saved searches, and browsing patterns.'
    },
    {
      icon: Camera,
      title: 'Product Data',
      description: 'Photos, descriptions, pricing, condition reports, and product specifications.'
    },
    {
      icon: TruckIcon,
      title: 'Transaction Details',
      description: 'Payment information, shipping addresses, delivery status, and communication logs.'
    },
    {
      icon: Users,
      title: 'Communication Records',
      description: 'Messages between buyers and sellers, negotiation history, and support interactions.'
    }
  ]

  const dataUsages = [
    { icon: Search, text: 'To match you with job opportunities based on your skills and preferences.' },
    { icon: Bell, text: 'To send automated job alerts, investor leads, or career advice via WhatsApp or email.' },
    { icon: MessageCircle, text: 'To enable voice/chat interactions with our AI Coach or Recruiter over WhatsApp and calls.' },
    { icon: BarChart3, text: 'To improve platform performance and enhance user experience using aggregated analytics.' },
    { icon: IdCard, text: 'To verify identity for subscription and access to premium services.' },
    { icon: Store, text: 'To facilitate buying and selling transactions on our marketplace platform.' },
    { icon: DollarSign, text: 'To process payments, manage escrow services, and handle financial transactions.' },
    { icon: TruckIcon, text: 'To coordinate shipping, delivery tracking, and logistics for marketplace orders.' }
  ]

  const sharingTypes = [
    { icon: Briefcase, text: 'Verified employers or recruiters (only after your consent).' },
    { icon: CreditCard, text: 'Payment processors (Stripe, Paystack) for billing and verification.' },
    { icon: Settings, text: 'Service providers (e.g., OpenAI, Google Cloud, Signalwire) to power features.' },
    { icon: Gavel, text: 'Law enforcement or regulators when required to comply with legal obligations.' },
    { icon: Users, text: 'Other marketplace users for transaction purposes (contact info, shipping details).' },
    { icon: TruckIcon, text: 'Shipping partners and logistics providers for order fulfillment.' }
  ]

  const userRights = [
    {
      icon: Eye,
      title: 'Access',
      description: 'Right to access the personal data we hold about you.'
    },
    {
      icon: Edit,
      title: 'Correction',
      description: 'Right to request correction or deletion of your data.'
    },
    {
      icon: XCircle,
      title: 'Withdraw Consent',
      description: 'Right to withdraw consent at any time (e.g., unsubscribe).'
    },
    {
      icon: Download,
      title: 'Portability',
      description: 'Right to receive your data in a usable format upon request.'
    }
  ]

  // Methods
  const toggleSidebar = () => {
    setSidebarVisible(!sidebarVisible)
  }

  const scrollToSection = (sectionNumber) => {
    const element = document.getElementById(`section-${sectionNumber}`)
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' })
      setSidebarVisible(false)
    }
  }

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }

  const handleScroll = () => {
    const scrollTop = window.pageYOffset
    const docHeight = document.documentElement.scrollHeight - window.innerHeight
    const progress = (scrollTop / docHeight) * 100
    setScrollProgress(progress)
    
    setShowBackToTop(scrollTop > 500)
    
    // Update active section
    const sections = document.querySelectorAll('.content-section')
    let currentSection = 1
    
    sections.forEach((section, index) => {
      const rect = section.getBoundingClientRect()
      if (rect.top <= 200 && rect.bottom > 200) {
        currentSection = index + 1
      }
    })
    
    setActiveSection(currentSection)
  }

  // Effects
  useEffect(() => {
    window.addEventListener('scroll', handleScroll)
    handleScroll()
    
    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  return (
    <div className="min-h-screen bg-gradient-to-br from-[#5C3AEB] via-[#5C3AEB] to-[#342299] relative overflow-x-hidden">
      {/* Animated Background */}
      <div className="fixed inset-0 pointer-events-none z-0">
        <div className="absolute w-72 h-72 bg-white/10 rounded-full top-10 left-10 animate-float"></div>
        <div className="absolute w-48 h-48 bg-white/10 rounded-full top-60 right-15 animate-float-delayed"></div>
        <div className="absolute w-36 h-36 bg-white/10 rounded-full bottom-20 left-3/5 animate-float-slow"></div>
        <div className="absolute inset-0 bg-grid-pattern opacity-5"></div>
      </div>

      {/* Header with Glass Effect */}
      <header className="relative z-10 py-8 lg:py-16">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 items-center">
            <button
              onClick={() => router.back()}
              className="justify-self-start bg-white/10 backdrop-blur-xl border border-white/20 rounded-2xl p-3 hover:bg-white/20 transition-all duration-300 hover:scale-105"
            >
              <ArrowLeft className="w-6 h-6 text-white" />
            </button>
            
            <div className="text-center lg:col-span-1">
              <h1 className="text-4xl md:text-5xl lg:text-6xl font-black text-white mb-4">
                Privacy Policy
                <div className="w-24 h-1 bg-gradient-to-r from-[#00C4A7] to-[#FF6B6B] rounded-full mx-auto mt-4"></div>
              </h1>
              <p className="text-white/80 text-lg">Last updated: July 13, 2025</p>
            </div>
            
            <div className="justify-self-end hidden lg:flex">
              <div className="w-20 h-20 bg-white/10 backdrop-blur-xl border border-white/20 rounded-full flex items-center justify-center animate-pulse">
                <Shield className="w-8 h-8 text-white" />
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Sidebar Navigation */}
      <nav className={`fixed top-0 left-0 w-80 h-screen bg-gray-900/95 backdrop-blur-xl border-r border-white/20 z-50 transform transition-transform duration-300 overflow-y-auto ${sidebarVisible ? 'translate-x-0' : '-translate-x-full'}`}>
        <div className="p-8 border-b border-white/10 flex justify-between items-center">
          <h3 className="text-white font-semibold text-lg">Quick Navigation</h3>
          <button onClick={toggleSidebar} className="text-white p-2 hover:bg-white/10 rounded-lg transition-colors">
            <X className="w-6 h-6" />
          </button>
        </div>
        <ul className="py-4">
          {sections.map((section, index) => (
            <li key={index}>
              <button 
                onClick={() => scrollToSection(index + 1)}
                className={`w-full flex items-center px-8 py-4 text-left transition-all duration-300 border-l-4 ${
                  activeSection === index + 1 
                    ? 'text-white bg-[#5C3AEB]/20 border-[#5C3AEB]' 
                    : 'text-white/70 hover:text-white hover:bg-white/5 border-transparent'
                }`}
              >
                <span className="font-bold mr-4 opacity-50">{String(index + 1).padStart(2, '0')}</span>
                <span className="font-medium">{section.title}</span>
              </button>
            </li>
          ))}
        </ul>
      </nav>

      {/* Mobile Navigation Toggle */}
      <button 
        onClick={toggleSidebar}
        className="fixed top-8 right-4 z-40 bg-white/10 backdrop-blur-xl border border-white/20 rounded-2xl p-3 text-white hover:bg-white/20 transition-all duration-300 flex items-center gap-2"
      >
        <Menu className="w-5 h-5" />
        <span className="text-sm font-medium">Contents</span>
      </button>

      {/* Main Content */}
      <main className="relative z-5 pb-8">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 space-y-16">
          
          {/* Introduction Section */}
          <section id="section-1" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                01
              </div>
              <h2 className="text-3xl font-bold text-white">Introduction</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8 hover:bg-white/15 transition-all duration-300 hover:scale-[1.02]">
              <p className="text-white leading-relaxed text-lg">
                At <strong className="bg-gradient-to-r from-[#00C4A7] to-[#FF6B6B] bg-clip-text text-transparent">Qwesi</strong> ("we", "our", or "us"), accessible at{' '}
                <a href="https://www.qwesi.org" className="text-[#00C4A7] hover:text-[#FF6B6B] border-b border-transparent hover:border-current transition-all">www.qwesi.org</a>, we prioritize your privacy. 
                This Privacy Policy outlines how we collect, use, disclose, and safeguard your personal data when you 
                interact with our servicesâ€” including our website, WhatsApp assistant, call-based platforms, and marketplace features for buying and selling.
              </p>
            </div>
          </section>

          {/* Information We Collect */}
          <section id="section-2" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                02
              </div>
              <h2 className="text-3xl font-bold text-white">Information We Collect</h2>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {informationTypes.map((item, index) => (
                <div key={index} className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-2xl p-6 hover:bg-white/15 transition-all duration-300 hover:scale-105">
                  <div className="w-12 h-12 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-xl flex items-center justify-center mb-4">
                    <item.icon className="w-6 h-6 text-white" />
                  </div>
                  <h3 className="text-white font-bold text-lg mb-2">{item.title}</h3>
                  <p className="text-white/80 text-sm leading-relaxed">{item.description}</p>
                </div>
              ))}
            </div>
          </section>

          {/* Marketplace Data Collection */}
          <section id="section-3" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                03
              </div>
              <h2 className="text-3xl font-bold text-white">Marketplace Data Collection</h2>
            </div>
            <div className="space-y-6">
              <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
                <p className="text-white leading-relaxed text-lg mb-8">
                  When you use our marketplace to buy or sell items, we collect additional information to facilitate secure transactions and provide the best user experience.
                </p>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {marketplaceDataTypes.map((item, index) => (
                    <div key={index} className="flex items-start gap-4 p-4 bg-white/5 rounded-xl border border-white/10">
                      <div className="w-10 h-10 bg-gradient-to-r from-[#00C4A7] to-[#FF6B6B] rounded-lg flex items-center justify-center flex-shrink-0">
                        <item.icon className="w-5 h-5 text-white" />
                      </div>
                      <div>
                        <h4 className="text-white font-semibold mb-1">{item.title}</h4>
                        <p className="text-white/70 text-sm">{item.description}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </section>

          {/* How We Use Your Data */}
          <section id="section-4" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                04
              </div>
              <h2 className="text-3xl font-bold text-white">How We Use Your Data</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <div className="space-y-6">
                {dataUsages.map((usage, index) => (
                  <div key={index} className="flex items-start gap-4">
                    <div className="w-10 h-10 bg-[#5C3AEB]/20 rounded-lg flex items-center justify-center flex-shrink-0">
                      <usage.icon className="w-5 h-5 text-[#5C3AEB]" />
                    </div>
                    <p className="text-white leading-relaxed">{usage.text}</p>
                  </div>
                ))}
              </div>
            </div>
          </section>

          {/* WhatsApp and Call Data Usage */}
          <section id="section-5" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                05
              </div>
              <h2 className="text-3xl font-bold text-white">WhatsApp and Call Data Usage</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <div className="flex justify-center gap-8 mb-8">
                <div className="w-15 h-15 bg-green-500 rounded-full flex items-center justify-center">
                  <MessageCircleMore className="w-8 h-8 text-white" />
                </div>
                <div className="w-15 h-15 bg-[#5C3AEB] rounded-full flex items-center justify-center">
                  <Phone className="w-8 h-8 text-white" />
                </div>
              </div>
              <div className="space-y-6 text-white leading-relaxed">
                <p>
                  When you interact with Qwesi via WhatsApp, your messages, audio files, and contact information 
                  are securely processed through Meta's WhatsApp Business API and used only to support your job or 
                  recruitment journey, as well as marketplace transactions.
                </p>
                <p>
                  Phone calls to Qwesi may be logged for support or verification. We do not record conversations 
                  unless explicitly stated and agreed to by the user for quality improvement or documentation purposes.
                </p>
              </div>
            </div>
          </section>

          {/* Buyer and Seller Interactions */}
          <section id="section-6" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                06
              </div>
              <h2 className="text-3xl font-bold text-white">Buyer and Seller Interactions</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <div className="space-y-6 text-white leading-relaxed">
                <p>
                  Our marketplace facilitates direct communication between buyers and sellers. We collect and process:
                </p>
                <ul className="space-y-4">
                  <li className="flex items-start gap-3">
                    <div className="w-2 h-2 bg-[#00C4A7] rounded-full mt-2"></div>
                    <span><strong>Transaction Communications:</strong> Messages, negotiations, and agreements between parties</span>
                  </li>
                  <li className="flex items-start gap-3">
                    <div className="w-2 h-2 bg-[#00C4A7] rounded-full mt-2"></div>
                    <span><strong>Rating and Reviews:</strong> Feedback provided by buyers and sellers about their experience</span>
                  </li>
                  <li className="flex items-start gap-3">
                    <div className="w-2 h-2 bg-[#00C4A7] rounded-full mt-2"></div>
                    <span><strong>Dispute Resolution:</strong> Information provided during complaint or dispute processes</span>
                  </li>
                  <li className="flex items-start gap-3">
                    <div className="w-2 h-2 bg-[#00C4A7] rounded-full mt-2"></div>
                    <span><strong>Delivery Information:</strong> Shipping addresses, delivery confirmations, and tracking data</span>
                  </li>
                </ul>
              </div>
            </div>
          </section>

          {/* Sharing of Information */}
          <section id="section-7" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                07
              </div>
              <h2 className="text-3xl font-bold text-white">Sharing of Information</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <div className="bg-green-500/20 border border-green-500/30 rounded-2xl p-6 mb-8 flex items-center gap-4">
                <Lock className="w-8 h-8 text-green-400" />
                <strong className="text-green-400 text-lg">Qwesi does not sell your data.</strong>
              </div>
              <p className="text-white mb-6">We may share information with:</p>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {sharingTypes.map((share, index) => (
                  <div key={index} className="flex items-center gap-4 p-4 bg-white/5 rounded-xl border border-white/10">
                    <div className="w-9 h-9 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-lg flex items-center justify-center flex-shrink-0">
                      <share.icon className="w-4 h-4 text-white" />
                    </div>
                    <p className="text-white text-sm">{share.text}</p>
                  </div>
                ))}
              </div>
            </div>
          </section>

          {/* Continue with remaining sections... */}
          {/* For brevity, I'll include the remaining sections in the same pattern */}
          
          {/* Third-Party Services */}
          <section id="section-8" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                08
              </div>
              <h2 className="text-3xl font-bold text-white">Third-Party Services</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <p className="text-white leading-relaxed mb-6">
                We integrate with external APIs including but not limited to OpenAI, WhatsApp Business API, Stripe, and various shipping providers. Each of these platforms has its own privacy policies. By using Qwesi, you also agree to their terms where applicable.
              </p>
              <div className="flex flex-wrap justify-center gap-4">
                {['OpenAI', 'WhatsApp', 'Stripe', 'Google Cloud', 'PayStack', 'FedEx', 'DHL'].map((partner) => (
                  <div key={partner} className="bg-white/10 border border-white/20 rounded-full px-4 py-2 text-white font-medium">
                    {partner}
                  </div>
                ))}
              </div>
            </div>
          </section>

          {/* Data Retention */}
          <section id="section-9" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                09
              </div>
              <h2 className="text-3xl font-bold text-white">Data Retention</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <p className="text-white leading-relaxed">
                We retain your personal data for as long as needed to provide our services, fulfill legal 
                obligations, resolve disputes, or enforce our agreements. Transaction records may be kept longer 
                for tax and legal compliance purposes. You can request deletion of your data 
                at any time by contacting{' '}
                <a href="mailto:info@qwesi.org" className="text-[#00C4A7] hover:text-[#FF6B6B] border-b border-transparent hover:border-current transition-all">info@qwesi.org</a>.
              </p>
            </div>
          </section>

          {/* Children's Privacy */}
          <section id="section-10" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                10
              </div>
              <h2 className="text-3xl font-bold text-white">Children's Privacy</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <div className="flex justify-center mb-6">
                <div className="w-15 h-15 bg-pink-500/20 border border-pink-500/30 rounded-full flex items-center justify-center">
                  <User className="w-8 h-8 text-pink-400" />
                </div>
              </div>
              <p className="text-white leading-relaxed mb-6">
                Qwesi is designed to be accessible and beneficial to users of all ages, including children. We 
                are committed to protecting the privacy of minors and comply with applicable laws such as the 
                Children's Online Privacy Protection Act (COPPA).
              </p>
              <div className="space-y-4">
                {[
                  { icon: Shield, text: 'Parental consent required for users under 13' },
                  { icon: Eye, text: 'Parents can review, update, or delete child\'s information' },
                  { icon: Ban, text: 'No ads or marketing to minors' }
                ].map((feature, index) => (
                  <div key={index} className="flex items-center gap-4 p-4 bg-pink-500/10 rounded-xl">
                    <feature.icon className="w-5 h-5 text-pink-400" />
                    <span className="text-white">{feature.text}</span>
                  </div>
                ))}
              </div>
            </div>
          </section>

          {/* Your Rights */}
          <section id="section-11" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                11
              </div>
              <h2 className="text-3xl font-bold text-white">Your Rights</h2>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {userRights.map((right, index) => (
                <div key={index} className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-2xl p-6 text-center hover:bg-white/15 transition-all duration-300 hover:scale-105">
                  <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center mx-auto mb-4">
                    <right.icon className="w-8 h-8 text-white" />
                  </div>
                  <h3 className="text-white font-bold text-lg mb-2">{right.title}</h3>
                  <p className="text-white/80 text-sm leading-relaxed">{right.description}</p>
                </div>
              ))}
            </div>
          </section>

          {/* Security */}
          <section id="section-12" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                12
              </div>
              <h2 className="text-3xl font-bold text-white">Security</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8 flex flex-col lg:flex-row items-center gap-8">
              <div className="relative flex-shrink-0">
                <div className="w-36 h-36 relative">
                  {/* Rotating security layers */}
                  <div className="absolute inset-0 border-4 border-[#5C3AEB]/30 rounded-full animate-spin-slow"></div>
                  <div className="absolute inset-4 border-4 border-[#00C4A7]/50 rounded-full animate-spin-reverse"></div>
                  <div className="absolute inset-8 border-4 border-[#FF6B6B]/70 rounded-full animate-spin-slow"></div>
                  <div className="absolute inset-12 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center">
                    <Shield className="w-8 h-8 text-white" />
                  </div>
                </div>
              </div>
              <div className="text-white leading-relaxed">
                <p>
                  We implement strong technical and organizational safeguards to protect your information from 
                  loss, misuse, or unauthorized access. These include secure data encryption, access control, regular 
                  security audits, and compliance with industry standards for marketplace transactions.
                </p>
              </div>
            </div>
          </section>

          {/* Changes to This Policy */}
          <section id="section-13" className="content-section scroll-mt-8">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                13
              </div>
              <h2 className="text-3xl font-bold text-white">Changes to This Policy</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8">
              <p className="text-white leading-relaxed">
                We may update this Privacy Policy occasionally to reflect changes in our services, legal requirements, 
                or business practices. The latest version will always be available on our website. Significant changes 
                will be communicated via email, WhatsApp, or through notifications on our platform.
              </p>
            </div>
          </section>

          {/* Contact Us */}
          <section id="section-14" className="content-section scroll-mt-8 mb-16">
            <div className="flex items-center gap-4 mb-8">
              <div className="w-15 h-15 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white font-black text-xl shadow-xl">
                14
              </div>
              <h2 className="text-3xl font-bold text-white">Contact Us</h2>
            </div>
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-3xl p-8 text-center">
              <h3 className="text-white font-bold text-xl mb-2">Get in Touch</h3>
              <p className="text-white/80 mb-8">Questions about this Privacy Policy or your personal data?</p>
              
              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <a 
                  href="mailto:info@qwesi.org"
                  className="flex flex-col items-center gap-4 p-6 bg-white/5 rounded-2xl border border-white/10 hover:bg-white/10 transition-all duration-300 hover:scale-105 text-decoration-none"
                >
                  <div className="w-12 h-12 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center">
                    <Mail className="w-6 h-6 text-white" />
                  </div>
                  <div>
                    <div className="text-white/60 text-xs uppercase tracking-wider mb-1">Email</div>
                    <div className="text-white font-semibold">info@qwesi.org</div>
                  </div>
                </a>
                
                <a 
                  href="tel:+12082796057"
                  className="flex flex-col items-center gap-4 p-6 bg-white/5 rounded-2xl border border-white/10 hover:bg-white/10 transition-all duration-300 hover:scale-105 text-decoration-none"
                >
                  <div className="w-12 h-12 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center">
                    <Phone className="w-6 h-6 text-white" />
                  </div>
                  <div>
                    <div className="text-white/60 text-xs uppercase tracking-wider mb-1">Phone</div>
                    <div className="text-white font-semibold">+1 208 279 6057</div>
                  </div>
                </a>
                
                <div className="flex flex-col items-center gap-4 p-6 bg-white/5 rounded-2xl border border-white/10">
                  <div className="w-12 h-12 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center">
                    <MapPin className="w-6 h-6 text-white" />
                  </div>
                  <div>
                    <div className="text-white/60 text-xs uppercase tracking-wider mb-1">Address</div>
                    <div className="text-white font-semibold text-center">DeReimer Street, New York, NY 10745, USA</div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </main>

      {/* Scroll Progress */}
      <div className="fixed top-0 left-0 w-full h-1 z-50 bg-white/10">
        <div 
          className="h-full bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] transition-all duration-100 ease-out"
          style={{ width: `${scrollProgress}%` }}
        />
      </div>

      {/* Back to Top */}
      {showBackToTop && (
        <button 
          onClick={scrollToTop}
          className="fixed bottom-8 right-8 w-14 h-14 bg-gradient-to-r from-[#5C3AEB] to-[#00C4A7] rounded-full flex items-center justify-center text-white shadow-xl hover:scale-110 transition-all duration-300 z-40"
        >
          <ArrowUp className="w-6 h-6" />
        </button>
      )}

      {/* Sidebar Overlay */}
      {sidebarVisible && (
        <div 
          className="fixed inset-0 bg-black/50 backdrop-blur-sm z-40"
          onClick={toggleSidebar}
        />
      )}

      <style jsx>{`
        .animate-float {
          animation: float 20s ease-in-out infinite;
        }
        .animate-float-delayed {
          animation: float 20s ease-in-out infinite;
          animation-delay: -7s;
        }
        .animate-float-slow {
          animation: float 20s ease-in-out infinite;
          animation-delay: -14s;
        }
        .animate-spin-slow {
          animation: spin 10s linear infinite;
        }
        .animate-spin-reverse {
          animation: spin 8s linear infinite reverse;
        }
        .bg-grid-pattern {
          background-image: 
            linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
            linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
          background-size: 50px 50px;
          animation: grid-move 30s linear infinite;
        }
        
        @keyframes float {
          0%, 100% { transform: translate(0, 0) rotate(0deg); }
          25% { transform: translate(20px, -20px) rotate(1deg); }
          50% { transform: translate(-10px, -30px) rotate(-1deg); }
          75% { transform: translate(-20px, 10px) rotate(0.5deg); }
        }
        
        @keyframes grid-move {
          0% { transform: translate(0, 0); }
          100% { transform: translate(50px, 50px); }
        }
        
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
        
        .content-section {
          scroll-margin-top: 2rem;
        }
        
        a {
          text-decoration: none;
        }
      `}</style>
    </div>
  )
}

export default PrivacyPolicy